import React, { Component } from 'react'
import styled from 'styled-components'

class Subscribe extends Component {
  constructor(props) {
    super(props)
    this.state = {
      email: '',
      subscribed: false
    }
  }

  validateEmail = (email) => {
    // eslint-disable-next-line
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return re.test(String(email).toLowerCase())
  }

  subscribe = () => {
    if (this.validateEmail(this.state.email)) {
      fetch(
        'https://us-central1-norblock-c98d8.cloudfunctions.net/contactForm_cannatrac?email=' +
          this.state.email
      ).then(function(res) {
        console.log(res)
      })
      this.setState({
        subscribed: true,
        email: ''
      })

      setTimeout(
        function() {
          this.setState({
            subscribed: false
          })
        }.bind(this),
        1000
      )
    } else {
      alert('Invalid email address.')
    }
  }

  handleChange = (e) => {
    this.setState({
      email: e.target.value
    })
  }

  render() {
    return (
      <Main>
        <Heading>Subscribe to our newsletter for updates</Heading>
        <InputsButton>
          {/* <Input placeholder="Name" /> */}
          <Input
            placeholder="E-mail"
            value={this.state.email}
            onChange={this.handleChange}
          />
          <SubBtn onClick={this.subscribe}>
            {this.state.subscribed ? 'Subscribed!' : 'Subscribe'}
          </SubBtn>
        </InputsButton>
      </Main>
    )
  }
}

export default Subscribe

const Main = styled.div`
  background-color: #4c3199;
  width: 70%;
  position: absolute;
  left: 0;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  margin-top: 200px;
  margin-bottom: 5em;
  padding-left: 10%;
  padding-bottom: 30px;

  @media (max-width: 700px) {
    margin-top: 50px;
    margin-bottom: 1em;
    width: 90%;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
  }
`

const Heading = styled.p`
  font-size: 32px;
  font-weight: bold;
  color: white;

  @media (max-width: 700px) {
    font-size: 22px;
  }
`

const InputsButton = styled.div`
  display: flex;

  @media (max-width: 700px) {
    flex-direction: column;
  }
`

const Input = styled.input`
  border: none;
  padding: 5px 10px;
  margin-right: 20px;
  min-width: 170px;
`

const SubBtn = styled.button`
  margin: 0;
  padding: 7px 45px;
  background-color: white;
  border-radius: 40px;
  color: #41129a;
  border: 0;
  font-family: 'Futura', sans-serif;
  font-size: 16px;

  @media (max-width: 700px) {
    width: 50%;
    margin-top: 10px;
    font-size: 12px;
  }
`
